    <el-dialog width="1000px" :visible.sync="showwrmDialog" custom-class="yjDialog">
      <div slot="title" class="head_title">
        <p>
          {{boxtitle}}
        </p>
        <div class="t_nav">
          <ul class="clearfix">
            <li @click="dialogNavClick(key)" v-for="(item,key) in dialogNav" :key="key" :class="dialogNavIndex>=key?'current':''">
              <i>{{key+1}}</i>
              {{item.name}}
            </li>
          </ul>
        </div>
      </div>
      <div class="dialogContent">
        <div class="leftBox width100" v-if="dialogNavIndex == 0">
          <el-timeline>
            <el-timeline-item icon="el-icon-message-solid" type='primary' size='large' :timestamp="currentwrnLogDetail.nowTm?currentwrnLogDetail.nowTm:''" placement="top">
              <el-card>
                <h4>实时信息</h4>
                <p v-if="currentwrnLogDetail.nowTm">{{currentwrnLogDetail.nowTm +'--'+currentwrnLogDetail.warnTm+' '+currentwrnLogDetail.nm}} </p>
              </el-card>
            </el-timeline-item>
            <el-timeline-item icon="el-icon-tickets" type='primary' size='large' :timestamp="currentwrnLogDetail.warnTm?currentwrnLogDetail.warnTm:''" placement="top">
              <el-card>
                <h4>产生预警</h4>
                <p>{{currentwrnLogDetail.warnContent?currentwrnLogDetail.warnContent:''}}</p>
              </el-card>
            </el-timeline-item>
            <el-timeline-item icon="el-icon-phone" type='primary' size='large' :timestamp="currentwrnLogDetail.noticeTm?currentwrnLogDetail.noticeTm:''" placement="top">
              <el-card>
                <h4>预警通知</h4>
                <p v-for="(notice, index) in currentwrnLogDetail.notices">{{notice}}</p>
              </el-card>
            </el-timeline-item>
            <el-timeline-item icon="el-icon-s-promotion" type='primary' size='large' :timestamp="currentwrnLogDetail.pubTm?currentwrnLogDetail.pubTm:''" placement="top">
              <el-card>
                <h4>预警发布</h4>
                <div v-for="(pubMsgs, index) in currentwrnLogDetail.pubMsgsGroup">
                  <p>内容:{{pubMsgs.content}} </p>
                  <p>接收人:{{pubMsgs.receiveUser}} </p>
                </div>
              </el-card>
            </el-timeline-item>
            <el-timeline-item icon="el-icon-close" type='primary' size='large' :timestamp="currentwrnLogDetail.closeTm?currentwrnLogDetail.closeTm:''" placement="top">
              <el-card>
                <h4>预警关闭</h4>
                <p>{{currentwrnLogDetail.closerName}}</p>
              </el-card>
            </el-timeline-item>
          </el-timeline>
        </div>
        <div class="leftBox width100" v-if="dialogNavIndex == 1" style="padding:0px">
          <div class="lrControl">
            <div class="lrLeft">
              <ul>
                <li v-for="(item,key) in lrControlNav" :key="key" :class="lrControlNavIndex ==key?'currentLi':''" @click="lrNavClick(key)">
                  <i></i>
                  {{item.name}}
                </li>
              </ul>
            </div>
            <div class="lrRight">
              <div class="lrModel" v-if="lrControlNavIndex == 0">
                <ul class="c_modul">
                  <li>
                    <span class="fSpan">规则名称 <span style="color: red">*</span> </span>
                    <div class="rDiv">
                      <el-input v-model="setobject.rule.nm"></el-input>
                    </div>
                  </li>
                  <li>
                    <span class="fSpan">规则类型:</span>
                    <div class="rDiv">
                      <el-radio v-model="setobject.rule.grp" label="wrn">预警</el-radio>
                      <el-radio v-model="setobject.rule.grp" label="crt">提醒</el-radio>
                      <el-radio v-model="setobject.rule.grp" label="alm">故障报警</el-radio>
                    </div>
                  </li>
                  <li>
                    <span class="fSpan">重复间隔:</span>
                    <div class="rDiv">
                      <el-checkbox ref="edit_rpt" @change="handleCheckrptValChange" checked="!setobject.rule.rptVal == '' ">是否启用重复间隔</el-checkbox>
                    </div>
                  </li>
                  <li id="edit_rpt_tr">
                    <span class="fSpan">&nbsp;&nbsp;</span>
                    <div class="rDiv">
                      <table>
                        <tr>
                          <td>
                            <el-input v-model="setobject.rule.rptVal"></el-input>
                          </td>
                          <td>
                            <el-select v-model="setobject.rule.rptUnit" placeholder="请选择">
                              <el-option v-for="item in rptUnitoptions " :key="item.value" :label="item.label" :value="item.value">
                              </el-option>
                            </el-select>
                          </td>
                          <td>以内不再触发</td>
                        </tr>
                      </table>
                    </div>
                  </li>
                  <li>
                    <span class="fSpan">自动关闭:</span>
                    <div class="rDiv">
                      <el-checkbox ref="edit_cls" @change="handleCheckclsValChange" checked="!setobject.rule.clsVal == ''  ">是否启用自动关闭</el-checkbox>
                    </div>
                  </li>
                  <li id="edit_cls_tr">
                    <span class="fSpan">&nbsp;&nbsp;</span>
                    <div class="rDiv">
                      <table>
                        <tr>
                          <td>
                            <el-input v-model="setobject.rule.clsVal"></el-input>
                          </td>
                          <td>
                            <el-select v-model="setobject.rule.clsUnit" placeholder="请选择">
                              <el-option v-for="item in rptUnitoptions " :key="item.value" :label="item.label" :value="item.value">
                              </el-option>
                            </el-select>
                          </td>
                          <td>以内不再触发</td>
                        </tr>
                      </table>
                    </div>
                  </li>
                  <li>
                    <span class="fSpan">启用规则:</span>
                    <div class="rDiv">
                      <el-checkbox @change="handleCheckstatusChange" checked="setobject.rule.status == 'enable'"></el-checkbox>
                    </div>
                  </li>
                  <li>
                    <span class="fSpan">触发方式:</span>
                    <div class="rDiv">
                      <el-select v-model="setobject.rule.trg" placeholder="请选择">
                        <el-option v-for="item in trgoptions " :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                      </el-select>
                    </div>
                  </li>
                  <li>
                    <span class="fSpan"> </span>
                    <div class="rDiv">
                      <el-dropdown @command="sumTypeCkeck">
                       
                        <el-button type="primary">
                          <i class="el-icon-plus"></i> 添加条件 <i class="el-icon-caret-bottom"></i>
