                      <input v-model="item.wechat_id" type="hidden"></input>
                      <input v-model="item.wechat_nm" type="hidden"></input>
                    </div>
                    <div class="rDiv" v-if="item.respTp=='sms' ">
                      <el-input type="textarea" :disabled="item.enable==0" v-model="item.sms_txt" placeholder="请输入备注" :rows="3"></el-input>
                      <input v-model="item.sms_id" type="hidden"></input>
                      <input v-model="item.sms_nm" type="hidden"></input>
                    </div>
                  </li>
                </ul>
                <div class="toorbar-wrap">
                  <el-button @click="sumcommde">提交</el-button>
                  <el-button type="noticonsecondBtn" @click="showwrmDialog=false">取消</el-button>
                </div>
              </div>
              <div class="lrModel" v-if="lrControlNavIndex === 1" style="height: 100%;">
                <work-simulation></work-simulation>
              </div>
              <div class="lrModel" v-if="lrControlNavIndex === 2" style="height: 100%;">
                <historical-data :stcds="currentwrnLogDetail.stcdsoption"></historical-data>
              </div>
              <div class="lrModel" v-if="lrControlNavIndex === 3" style="height: 100%;">
                <up-and-down-data :stcds="currentwrnLogDetail.stcdsoption"></up-and-down-data>
              </div>
              <div class="lrModel" v-if="lrControlNavIndex === 4" style="height: 100%;">
                <operations-maintenance></operations-maintenance>
              </div>
              <div class="lrModel" v-if="lrControlNavIndex === 5" style="height: 100%;">
                <operational-records></operational-records>
              </div>
            </div>
          </div>
        </div>
        <div class="leftBox" v-if="dialogNavIndex == 2">
          <ul class="c_modul">
            <li>
              <span class="fSpan">报警诊断：</span>
              <div class="rDiv">
                <el-radio v-model="policeRadio" label="1">设备故障</el-radio>
                <el-radio v-model="policeRadio" label="2">误传误报</el-radio>
                <el-radio v-model="policeRadio" label="3">运维失误</el-radio>
                <el-radio v-model="policeRadio" label="4">其他</el-radio>
                <el-input type="textarea" v-model="diacrisisReason" placeholder="请输入备注" :rows="3"></el-input>
              </div>
            </li>
            <li>
              <span class="fSpan">解决方案 ：</span>
              <div class="rDiv">
                <el-radio v-model="policeRadio" label="1">更换配件</el-radio>
                <el-radio v-model="policeRadio" label="2">更换试剂</el-radio>
                <el-radio v-model="policeRadio" label="3">其他</el-radio>
                <el-input type="textarea" v-model="diacrisisReason" placeholder="请输入备注" :rows="3"></el-input>
              </div>
