                <el-input type="textarea" v-model="diacrisisReason" placeholder="请输入备注" :rows="3"></el-input>
              </div>
            </li>
            <li>
              <span class="fSpan">预采取措施 ：</span>
              <div class="rDiv">
                <el-radio v-model="policeRadio" label="1">请求支援</el-radio>
                <el-radio v-model="policeRadio" label="2">其他</el-radio>
                <el-input type="textarea" v-model="diacrisisReason" placeholder="请输入备注" :rows="3"></el-input>
              </div>
