                      <option v-for="item in thresholdOp" :key="item.value" :label="item.label" :value="item.value"> </option>
                    </select>
                  </td>
                  <td>
                    <el-input style="width:200px;" id="item_thresholdVal" type="text" class="form-control inline">
                    </el-input>
                  </td>
                  <td>
                    时
                  </td>
                </tr>
              </table>
            </div>
          </li>
          <li id="item_rate" style="display:none">
            <span class="fSpan">条件设置 </span>
            <div class="rDiv">
              <table>
                <tr>
                  <td>
                    当时间在
                  </td>
                  <td>
                    <el-input id="item_rateTmVal" type="text" style="width:80px">
                    </el-input>
                  </td>
                  <td>
                    <el-select v-if="selectedititem!=null" id="item_rateTmUnit" placeholder="请选择">
                      <el-option v-for="item in rateops" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                  </td>
                  <td>
                    以内，数据
                  </td>
                  <td>
                    <el-select id="item_rateTrend" style="width:80px">
                      <el-option value="up">上升</el-option>
                      <el-option value="down">下降</el-option>
                    </el-select>
