                  </td>
                </tr>
              </table>
            </div>
          </li>
          <li id="item_flat" style="display:none">
            <span class="fSpan">条件设置 </span>
            <div class="rDiv">
              <table>
                <td>当时间在 </td>
                <td>
                  <el-input id="item_flatTmVal" type="text" style="width:80px" />
                </td>
                <td>
                  <el-select id="item_flatTmUnit" class="form-control inline" style="width:80px">
                    <el-option v-for="item in rateops" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </td>
                <td>
                  以内，数据变化范围
                </td>
                <td>
                  <el-select id="item_flatOp" style="width:80px">
                    <el-option value="l">&lt;</el-option>
                    <el-option value="le">&lt;=</el-option>
                  </el-select>
                </td>
                <td>
                  <el-input id="item_flatVal" type="text" style="width:80px" />
                </td>
                <td>
                  时
                </td>
              </table>
            </div>
          </li>
          <li id="item_delay" style="display:none">
            <span class="fSpan">条件设置 </span>
