                    <el-input id="item_delayStart" type="text" style="width:90px;" />
                  </td>
                  <td>
                    起，每隔
                  </td>
                  <td>
                    <el-input id="item_delayTmVal" type="text" style="width:100px;" />
                  </td>
                  <td>
                    <el-select id="item_delayTmUnit" style="width:100px;">
                      <el-option v-for="item in rateops" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                  </td>
                  <td>
                    检查是否收到数据，允许误差范围
                  </td>
                  <td>
                    <el-input id="item_delayBufVal" type="text" style="width:90px;" />
                  </td>
                  <td>
                    <el-select id="item_delayBufUnit" style="width:90px;">
                      <el-option v-for="item in rateops" :key="item.value" :label="item.label" :value="item.value">
                      </el-option>
                    </el-select>
                  </td>
                </tr>
              </table>
            </div>
          </li>
          <li id="item_none" style="display:none">
            <span class="fSpan">条件设置 </span>
            <div class="rDiv">
              <table>
                <tr>
                  <td>
                    当超过
                  </td>
                  <td>
                    <el-input id="item_noneTmVal" type="text" class="form-control inline" style="width:80px" />
                  </td>
                  <td>
                    <select id="item_noneTmUnit" class="eselect" style="width:80px">
                      <option v-for="item in rateops" :key="item.value" :label="item.label" :value="item.value">
                      </option>
                    </select>
                  </td>
                  <td>
                    未收到数据时
                  </td>
                </tr>
              </table>
            </div>
          </li>
        </ul>
      </div>
    </el-dialog>

          

  </div>
</template>
<script>
import eplUtil from "../../assets/js/eplUtil";

import workSimulation from "../operaAndmaint/workSimulation"; //工况模拟
import processLog from "./alarmDialog/processLog"; //流程日志
import historicalData from "./alarmDialog/historicalData"; //历史数据
import upAndDownData from "./alarmDialog/upAndDownData"; //上下游数据
import operationsMaintenance from "./alarmDialog/operationsMaintenance"; //运维情况
import operationalRecords from "./alarmDialog/operationalRecords"; //操作记录


export default {
  name: '',
  components: {
    workSimulation,
    processLog,
    historicalData,
    upAndDownData,
    operationsMaintenance,
    operationalRecords
  },
  data() {
    return {

      dialogNav: [
        { name: "报警信息" },
        { name: "远程解决" },
        { name: "报警关闭" }
      ], //报警处理弹框顶部导航
      lrControlNav: [
        { name: "预警设置" },
        { name: "预警发送" },
        { name: "历史过程" },
        { name: "水位剖面" }
       // { name: "运维数据" },
        //{ name: "操作记录" }
      ],
      datatypeoptions: [{
        value: 'crt',
        label: '生产数据'
      }, {
        value: 'raw',
        label: '原始数据'
      }],
      thresholdOp: [{
          value: 'g',
          label: '&gt;'
        },
        {
          value: 'l',
          label: '&lt;'
        },
        {
          value: 'ge',
          label: '&gt;='
        },
        {
          value: 'le',
          label: '&lt;='
        },
        {
          value: 'lg',
          label: '&lt;&gt;'
        },
        {
          value: 'eq',
          label: '='
        }


      ],
      rateops: [{
          value: 'sec',
          label: '秒'
        },
        {
          value: 'min',
          label: '分钟'
        },
        {
          value: 'hour',
          label: '小时'
        },
        {
          value: 'day',
          label: '天'
        },
        {
          value: 'month',
          label: '月'
        },
        {
          value: 'year',
          label: '年'
        }
      ],

      itemrules: [],
      filterText: '',
      adcdtreedata: [],
      selectionruleitems: [],
      selectSiteMeas: [],
      defaultProps: {
        children: 'children',
        label: 'nm'
      },

      showwrmDialog: false, //预警处理弹框
      showitemcopyDialog: false, //规则复制窗体
      showedititemDialog: false, //编辑条件
      showtreesbox: false,
      selectedititem: null,
      editDialogTile: "编辑条件",
      currentwrnLogDetail: {},
      lrControlNavIndex: 0,
      dialogNavIndex: 0, //报警处理弹框顶部导航索引

      dateRange: [],
      setobject: {},

      rptUnitoptions: [{
          value: 'sec',
          label: '秒'
        }, {
          value: 'min',
          label: '分钟'
        }, {
          value: 'hour',
          label: '小时'
        }, {
          value: 'day',
          label: '天'
        }, {
          value: 'month',
          label: '月'
        },
        {
          value: 'year',
          label: '年'
        }

      ],

      trgoptions: [{
          value: 'any',
          label: '满足任一条件触发'
        },
        {
          value: 'all',
          label: '满足全部条件触发'
        }

      ],
      dpmoptions: [

        {
          value: 'threshold',
          label: '阈值'
        },
        {
          value: 'rate',
          label: '变化率'
        },
        {
          value: 'flat',
          label: '平坦'
        },
        {
          value: 'delay',
          label: '延迟'
        },

        {
          value: 'none',
          label: '无数据'
        }
      ],

      boxtitle:"预警管理",
      grp:"wrn",

      isEdit: false,
      nm: "", // 站点名称
      currentwrnData: [], // 预警待处理预警数据
      wrntableData: [], //预警全部数据
      currentwrnPage: 1, //预警分页组件当前页
      wrntotal: 0, //预警分页总条数
      pageSize_wrn: 5, //预警分页每页多少条数据

      almtotal: 0, //报警分页总条数
      crttotal: 0, //提醒分页总条数
      total:0,

      historywrncurentdata: [], // 历史预警数据
      historywrntableData: [], //预警全部数据
      currenthistorywrnPage: 1, //预警分页组件当前页
      historywrntotal: 0, //预警分页总条数
      pageSize_historywrn: 5, //预警分页每页多少条数据

      pageList: [5, 10, 15, 20], //设置每页多少条

    }
  },
  created() {
    this.$store.commit("setShowlefttree", true);
    this.initcoypTreeLayout();

  },
  computed: {

  },
  watch: {
    filterText(val) {
      this.$refs.adcdtree.filter(val);
    }
  },

  mounted() {

      this.getListCountByGrp( "alm");
      this.getListCountByGrp( "crt");
      this.getListCountByGrp( "wrn");


    this.getwrnList(1, 10,this.grp);
    this.gethistorywrnList(1, 10,this.grp);

   
    

  },
  methods: {

  handleClick(tab, event) 
  {  
      

     this.grp=tab.grp
     if(this.grp=="wrn")
     {
    this.boxtitle="预警管理"
     }
     else if(this.grp=="alm")
     {
    this.boxtitle="报警管理"
     }
    else 
    {
     this.boxtitle="提醒管理"
    }
      
     setTimeout(() => {
      this.getwrnList(1, 10,this.grp);
      this.gethistorywrnList(1, 10,this.grp);
     }, 10)
      
    
  },

    setResps() {
      var resp = this.data.resp;
      for (var i = 0; i < resp.length; i++) {
        var item = resp[i];
        var users = item.users;
        var id = [];
        var nm = [];
        for (var j = 0; j < users.length; j++) {
          id.push(users[j].userId);
          nm.push(users[j].userNm);
        }
        $("#edit_" + item.respTp + "_id").val(id.join(","));
        $("#edit_" + item.respTp + "_nm").val(nm.join(","));
        $("#edit_" + item.respTp + "_txt").val(nm.join(" "));
        $("#edit_" + item.respTp + "_cb").attr("checked", (item.enable == 1));
      }
      this.showRespUserCount("wechat");
      this.showRespUserCount("sms");

      this.onClickWechat();
      this.onClickSms();
    },


    showwrmDialogFrim(index, row,grp="wrn") {
      this.dialogNavIndex = 0;
      var _seit = this;


      _seit.$http.get("./static/iot/rule_mgmt/api/rule-service/rule.json", {

        params: {
          ruleId: row.ruleId

        }


      }).then((response) => {


        if (_seit.setobject == null) {
          var rule = {
            nm: "",
            grp: grp,
            rptVal: 2,
            rptUnit: "hour",
            clsVal: 24,
            clsUnit: "hour",
            trg: "any",
            status: "enable",
            editable: 1,
            visible: 1
          };

          _seit.setobject = {
            rule: rule,
            items: [],
            resp: []
          };

          _seit.isEdit = false;
        } else {
          _seit.isEdit = true;
        }



        for (var i = 0; i < response.data.data.items.length; i++) {
          response.data.data.items[i].TypeLabel = _seit.itemTypeLabel(response.data.data.items[i].type)
        }

        var resp = response.data.data.resp;
