    getRule() {
      var rule = this.setobject.rule;

      
      if (this.$refs.edit_cls.isChecked) {
      } else {
        this.setobject.rule.clsVal = "";
        this.setobject.rule.clsUnit = "";
      }




      if (this.$refs.edit_rpt.isChecked) {
        // rule.rptVal = $("#edit_rptVal").val();
        // rule.rptUnit = $("#edit_rptUnit").val();
      } else {
        this.setobject.rule.rptVal = "";
        this.setobject.rule.rptUnit = "";
      }



      // rule.status = $("#edit_status").is(":checked") ? "enable" : "disable";
      // rule.trg = $("#edit_trg").val();

      var resp = [];
      if (this.setobject.resp[0].enable) resp.push("微信");
      if (this.setobject.resp[1].enable) resp.push("短信");





      if (resp.length == 0) {
        this.setobject.rule.resp = "无";
      } else if (resp.length == 1) {
        this.setobject.rule.resp = resp[0];
      } else {
        this.setobject.rule.resp = resp.join("/");
      }

      this.setobject.rule.scp = "";
      this.setobject.rule.stMeas = "";

      var stMeasObj = {};
      var items = this.setobject.items;
      if (items && items.length > 0) {
        for (var i = 0; i < items.length; i++) {
          var meas = stMeasObj[items[i].stNm];
          if (meas) {
            var measArray = meas.split("/"); //以逗号作为分隔字符串
            var flag = true;
            for (var j = 0; j < measArray.length; j++) {
              if (measArray[j] == items[i].stMeasNm) {
                flag = false;
              }
            }
            if (flag) {
              stMeasObj[items[i].stNm] = meas + "/" + items[i].stMeasNm;
            }
          } else {
            stMeasObj[items[i].stNm] = items[i].stMeasNm;
          }
        }

        var stMeasArray = [];
        for (var stNm in stMeasObj) {
          stMeasArray.push(stNm + " - " + stMeasObj[stNm]);
        }

        if (stMeasArray.length > 0) {
          if (stMeasArray.length == 1) {
            this.setobject.rule.scp = "single";
            this.setobject.rule.stMeas = stMeasArray[0];
          } else {
            this.setobject.rule.scp = "multi";
            this.setobject.rule.stMeas = stMeasArray.join("<br>");
          }
        }
      }

    },



