          color: #666666;
          padding-right: 32px;
          position: relative;

          &:first-child {
            padding-left: 28px;
          }
        }

        .site-icon {
          position: absolute;
          width: 20px;
          height: 29px;
          background: url("../../assets/images/state-icon.png") no-repeat;
          left: 0;
          top: 0;
        }

        .content-text {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }

        .site-name {
          font-size: 16px;
          color: #333;
          font-weight: 700;
        }

        .label-text {
          color: #999999;
        }

        .progress-wrap {
          padding-top: 10px;

          .node-progress {
            padding-right: 15px;
            padding-left: 9px;
            position: relative;

            .first.spot {
              width: 0;
            }

            .spot {
              display: block;
              float: left;
              height: 3px;
              width: 25%;
              background: #d1def0;
              position: relative;

              &:before {
                content: "";
                position: absolute;
                width: 9px;
                height: 9px;
                border-radius: 50%;
                background: #d1def0;
                top: -3px;
                right: 0;
              }
            }

            .spot-active-wrap {
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              z-index: 2;
              padding-left: 9px;
              padding-right: 15px;

              &>span {
                display: block;
                float: left;
                width: 25%;
                height: 3px;
                position: relative;

                &:first-child {
                  width: 0;
                }
              }

              .spot-active {
                position: absolute;
                width: 9px;
                height: 9px;
                border-radius: 50%;
                right: 0;
                top: -3px;
                cursor: pointer;
              }
            }

            .stateA.spot-active-wrap {
              &>span {
                background: #ff3333;
              }

              .spot-active {
                background: #ff3333;
              }
            }

            .stateB.spot-active-wrap {
              &>span {
                background: #166bce;
              }

              .spot-active {
                background: #166bce;
              }
            }

            .stateC.spot-active-wrap {
              &>span {
                background: #36c25e;
              }

              .spot-active {
                background: #36c25e;
              }
            }
          }
        }

        .label-state {
          display: inline-block;
          vertical-align: top;
          width: 70px;
          height: 24px;
          line-height: 22px;
          text-align: center;
          border: 1px solid #166bce;
          border-radius: 12px 0;
          font-size: 12px;
          color: #166bce;
        }

        .label-state-wrap {
          padding-top: 8px;
        }

        .state-a {
          color: #36c25e;
          border-color: #36c25e;
        }

        .state-b {
          color: #ff3333;
          border-color: #ff3333;
        }
      }

      &>li:last-child {
        border-bottom: none;
      }
    }
  }
}

.state-radio-wrap {
  display: inline-block;
  vertical-align: top;
}

.search-box {
  display: inline-block;
  vertical-align: top;
  position: relative;

  height: 30px;
  line-height: 30px;

  .icon-sousuo {
    position: absolute;
    right: 10px;
    top: 0;
    color: #7c8fa4;
    cursor: pointer;
    font-size: 14px;
    z-index: 2;
  }

  .icon-sousuo:hover {
    color: #166bce;
  }

  .icon-sousuo:active {
    color: #1056a7;
  }

  .search-input {
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    height: 30px;
    top: 0;
    left: 0;
    padding-left: 5px;
    padding-right: 32px;
    outline: none;
    border: 1px solid #c6d1de;
  }
}

//报警处理弹框
.head_title {
  >p {
    >span {
      display: inline-block;
      cursor: pointer;
      width: 73px;
      height: 24px;
      text-align: center;
      font-size: 13px;
      font-weight: 400;
      line-height: 24px;
      border: 1px solid #b7d7f7;
      background: #e6f1fc;
      color: #166bce;
    }
  }

  .t_nav {
    padding: 0 20px;
    margin-bottom: 12px;

    li {
      float: left;
      cursor: pointer;
      font-size: 13px;
      height: 25px;
      line-height: 25px;
      font-weight: 400;
      margin-right: 355px;
      position: relative;
      color: #999;

      &:after {
        content: "";
        height: 2px;
        width: 354px;
        background: #c6d1de;
        position: absolute;
        left: 86px;
        top: 12px;
      }

      >i {
        display: inline-block;
        height: 20px;
        width: 20px;
        color: #fff;
        line-height: 20px;
        border-radius: 50%;
        background: #c6d1de;
        margin-right: 6px;
        font-style: normal;
      }

      &:last-child {
        margin-right: 0px;

        &:after {
          display: none;
        }
      }
    }

    .current {
      color: #1a1a1a;

      >i {
        background: #166bce;
      }
    }
  }
}

.dialogContent {
  height: 450px;
  background: #eff3f5;

  .c_modul {
    li {
      margin-bottom: 12px;

      .fSpan {
        display: inline-block;
        width: 115px;
        text-align: right;
        height: 30px;
        line-height: 30px;
        color: #1a1a1a;
        vertical-align: top;
      }

      .rDiv {
        display: inline-block;
        height: 30px;
        line-height: 30px;
        width: calc(100% - 122px);
      }
    }
  }

  >div {
    float: left;
  }

  .leftBox {
    margin-right: 6px;
    height: 100%;
    background: #fff;
    border: 1px solid #d9e6f7;
    width: calc(100% - 181px);
    padding: 15px;
    overflow: auto;

    .lrControl {
      height: 100%;

      .lrLeft {
        width: 120px;
        float: left;
        border-right: 1px solid #d9e6f7;
        height: 100%;
        background: #f5f5f5;
        padding: 16px 0 0 5px;

        li {
          height: 40px;
          line-height: 40px;
          cursor: pointer;
          padding-left: 26px;

          margin-bottom: 15px;
          position: relative;
          width: 112px;

          >i {
            display: none;
          }
        }

        .currentLi {
          background: #fff;
          width: 113px;
          margin-left: 2px;
          border-radius: 5px 0 0 5px;
          border: 1px solid #d9e6f7;
          border-right: 1px solid #fff;
          color: #166bce;
          font-weight: 700;

          >i {
            position: absolute;
            display: block;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            border: 1px solid #166bce;
            left: 10px;
            top: 15px;

            &::before {
              content: "";
              width: 4px;
              height: 4px;
              border-radius: 50%;
              border: 1px solid #166bce;
              position: absolute;
              left: 1px;
              top: 1px;
            }
          }
        }
      }

      .lrRight {
        width: calc(100% - 120px);
        float: left;
        height: 100%;

        .lrModel {
          padding: 12px;
        }
      }
    }
  }

  .rightBox {
    background: #fff;
    width: 175px;
    height: 100%;
    border: 1px solid #d9e6f7;

    >p {
      height: 33px;
      line-height: 33px;
      padding-left: 28px;
      border-bottom: 1px solid #d9e6f7;
      position: relative;

      &:before {
        position: absolute;
        content: "";
        width: 3px;
        height: 10px;
        background: #166bce;
        left: 10px;
        top: 12px;
      }
    }

    .timeline {
      padding: 12px 5px;
    }
  }
}

.toorbar-wrap {
  text-align: center;
  padding-top: 20px;

  .el-button {
    display: inline-block;
    vertical-align: middle;
  }
}

</style>



