  var eplUtil = {};
  eplUtil.defaultParam = function(type) {
    var param = {};

    if (type == "threshold") {
      param.thresholdOp = "ge";
      param.thresholdVal = "";

    } else if (type == "rate") {
      param.rateTmVal = "10";
      param.rateTmUnit = "min";
      param.rateTrend = "up";
      param.rateOp = "ge";
      param.rateVal = "";

    } else if (type == "flat") {
      param.flatTmVal = "10";
      param.flatTmUnit = "min";
      param.flatOp = "le";
      param.flatVal = "";

    } else if (type == "delay") {
      param.delayStart = "2016-01-01 08:00:00";
      param.delayTmVal = "10";
      param.delayTmUnit = "min";
      param.delayBufVal = "5";
      param.delayBufUnit = "min";

    } else if (type == "none") {
      param.noneTmVal = "10";
      param.noneTmUnit = "min";

    } else if (type == "waterarea") {
      param.waterareaOp = "ge";
      param.waterareaVal = "";

    } else if (type == "floatwaste") {
      param.floatwasteOp = "ge";
      param.floatwasteVal = "";

    } else if (type == "watergauge") {
      param.watergaugeOp = "ge";
      param.watergaugeVal = "";

    } else if (type == "watercolor") {
      param.watercolorOp = "ge";
      param.watercolorVal = "";

    } else if (type == "boat") {
      param.boatOp = "ge";
      param.boatVal = "";

    } else if (type == "fishing") {
      param.fishingOp = "ge";
      param.fishingVal = "";

    } else if (type == "swim") {
      param.swimOp = "ge";
      param.swimVal = "";

    } else if (type == "bluealgae") {
      param.bluealgaeOp = "ge";
      param.bluealgaeVal = "";

    }

    return param;
  };

  eplUtil.parseEpl = function(epl) {
    var param = {};
    var str = epl.split("|");
    for (var i = 1; i < str.length; i++) {
      var p = str[i].split("$");
      if (p.length > 1) {
        param[p[0]] = p[1];
      } else {
        pparam[p[0]] = "";
      }
    }
    return param;
  };

  eplUtil.generateEpl = function(type, dtp) {
    var epl = ["dtp$" + dtp];
    var param = this.defaultParam(type);
    for (var key in param) {
      var val = $("#item_" + key).val();
      epl.push(key + "$" + val);
    }
    return epl.join("|");
  };

  eplUtil.generateEplDesc = function(type, dtp) {
    var desc = "";

    if (type == "threshold") {
      desc += ruleConst.dtpLabel(dtp);
      desc += ruleConst.opLabel($("#item_thresholdOp").val());
      desc += $("#item_thresholdVal").val();

    } else if (type == "rate") {
      desc += $("#item_rateTmVal").val();
      desc += ruleConst.unitLabel($("#item_rateTmUnit").val());
      desc += "内，";
      desc += ruleConst.dtpLabel(dtp);
      desc += ruleConst.trendLabel($("#item_rateTrend").val());
      desc += "范围";
      desc += ruleConst.opLabel($("#item_rateOp").val());
      desc += $("#item_rateVal").val();

    } else if (type == "flat") {
      desc += $("#item_flatTmVal").val();
      desc += ruleConst.unitLabel($("#item_flatTmUnit").val());
      desc += "内，";
      desc += ruleConst.dtpLabel(dtp);
      desc += "变化范围";
      desc += ruleConst.opLabel($("#item_flatOp").val());
      desc += $("#item_flatVal").val();

    } else if (type == "delay") {
      desc += $("#item_delayStart").val();
      desc += "起，每隔";
      desc += $("#item_delayTmVal").val();
      desc += ruleConst.unitLabel($("#item_delayTmUnit").val());
      desc += "收到";
      desc += ruleConst.dtpLabel(dtp);
      desc += "，误差";
      desc += $("#item_delayBufVal").val();
      desc += ruleConst.unitLabel($("#item_delayBufUnit").val());

    } else if (type == "none") {
      desc += "超过";
      desc += $("#item_noneTmVal").val();
      desc += ruleConst.unitLabel($("#item_noneTmUnit").val());
      desc += "未收到";
      desc += ruleConst.dtpLabel(dtp);
    } else if (type == "waterarea") {
      desc += ruleConst.dtpLabel(dtp);
      desc += ruleConst.opLabel($("#item_waterareaOp").val());
      desc += $("#item_waterareaVal").val();
    } else if (type == "floatwaste") {
      desc += ruleConst.dtpLabel(dtp);
      desc += ruleConst.opLabel($("#item_floatwasteOp").val());
      desc += $("#item_floatwasteVal").val();
    } else if (type == "watergauge") {
      desc += ruleConst.dtpLabel(dtp);
      desc += ruleConst.opLabel($("#item_watergaugeOp").val());
      desc += $("#item_watergaugeVal").val();
    } else if (type == "watercolor") {
      desc += ruleConst.dtpLabel(dtp);
      desc += ruleConst.opLabel($("#item_watercolorOp").val());
      desc += $("#item_watercolorVal").val();
    } else if (type == "boat") {
      desc += ruleConst.dtpLabel(dtp);
      desc += ruleConst.opLabel($("#item_boatOp").val());
      desc += $("#item_boatVal").val();
    } else if (type == "fishing") {
      desc += ruleConst.dtpLabel(dtp);
      desc += ruleConst.opLabel($("#item_fishingOp").val());
      desc += $("#item_fishingVal").val();
    } else if (type == "swim") {
      desc += ruleConst.dtpLabel(dtp);
      desc += ruleConst.opLabel($("#item_swimOp").val());
      desc += $("#item_swimVal").val();
    } else if (type == "bluealgae") {
      desc += ruleConst.dtpLabel(dtp);
      desc += ruleConst.opLabel($("#item_bluealgaeOp").val());
      desc += $("#item_bluealgaeVal").val();
    }

    return desc;
  };
export default eplUtil;
 



